import { computed, reactive, ref } from 'vue'
import store from 'store'
import moment from 'moment'
import { useRoute } from 'vue-router'
import { useUser } from 'composable/user'

const confirmManuallyPricedModal = ref(false)

const transformItem = (item) => {
  return {
    ...item,
    conditions: item.conditions.data.map((condition) => ({
      name: condition.name,
      price: condition.price,
      rule: condition.rule,
      original_price: condition.original_price,
      is_manually_priced: condition.is_manually_priced,
      price_from_source: condition.price_from_source
    }))
  }
}
const transformItemGroup = (items) => {
  return items
    .map((productGroup) => productGroup?.items)
    .flat()
    .map((item) => transformItem(item))
}
const parsePrice = (price) => {
  const parsedPrice = typeof price === 'string' ? parseFloat(price) : price
  if (isNaN(parsedPrice)) return 0
  return parsedPrice
}
const transformItemConditionPrices = (itemConditions, percentage = 100) => {
  return itemConditions.map((itemCondition) => ({
    ...itemCondition,
    price: parsePrice(itemCondition.price) * (percentage / 100),
    original_price: parsePrice(itemCondition.price),
    is_manually_priced: false
  }))
}
const checkHasManuallyPricedItems = (items) => {
  const itemGroups = [...(items || [])]
  for (const itemGroup of itemGroups) {
    const items = itemGroup.items
    for (const item of items) {
      const { conditions = {} } = item
      const { data: conditionData = [] } = conditions
      for (const condition of conditionData) {
        if (condition.is_manually_priced) return true
      }
    }
  }
  return false
}
const resetManuallyPricedItemCondition = (itemCondition, percentage = 100) => {
  if (!itemCondition.is_manually_priced) return itemCondition
  return {
    ...itemCondition,
    price: parsePrice(itemCondition.original_price) * (percentage / 100),
    is_manually_priced: false
  }
}
const resetManuallyPricedItem = (item = {}, percentage = 100) => {
  return {
    ...item,
    conditions: {
      ...(item.conditions ? item.conditions : {}),
      data: [...(item.conditions?.data ? item.conditions.data : [])].map(
        (itemCondition) => {
          return resetManuallyPricedItemCondition(itemCondition, percentage)
        }
      )
    }
  }
}
const _resetManuallyPricedItems = (items = [], percentage = 100) => {
  const itemGroup = [...(Array.isArray(items) ? items : [])]
  return itemGroup.map((itemGroup) => {
    return {
      ...itemGroup,
      items: [...(Array.isArray(itemGroup.items) ? itemGroup.items : [])].map(
        (item) => {
          return resetManuallyPricedItem(item, percentage)
        }
      )
    }
  })
}

export const useBulkQuote = () => {
  const route = useRoute()

  const selectedTab = ref('active_item')

  const isBulkQuote = computed(() => ['bulkQuoteDetail'].includes(route.name))
  const tabBulkQuote = computed(() => {
    return [
      {
        name: 'Active',
        value: 'active_item'
      },
      {
        name: 'Reconciled',
        value: 'reconciled_item'
      }
    ]
  })
  return {
    isBulkQuote,
    tabBulkQuote,
    selectedTab
  }
}

export const useBulkQuoteItem = () => {
  const items = ref([])
  const loading = reactive({
    getBulkQuoteItems: false,
    getBulkQuoteItemInfo: false,
    updateQuotePercentage: false,
    deleteQuoteItem: false
  })
  const quotePercentage = ref(100)
  const totalItems = ref(0)
  const requestController = reactive({
    updateQuoteItem: null,
    updateQuotePercentage: null
  })
  const hasManuallyPricedItems = computed(() =>
    checkHasManuallyPricedItems(items.value)
  )

  const parseItems = (items = []) => {
    const itemGroup = {}
    Object.entries(items).forEach(([key, items]) => {
      items.forEach((item) => {
        const isExistKey = itemGroup.hasOwnProperty(key)
        if (isExistKey) {
          itemGroup[key].items.push({
            ...item,
            conditions: {
              data: item.condition || []
            }
          })
        } else {
          itemGroup[key] = {
            id: item.top_category_id,
            name: item.group_name,
            is_mac: item.is_mac,
            items: [
              {
                ...item,
                conditions: {
                  data: item.condition || []
                }
              }
            ]
          }
        }
      })
    })
    return Object.entries(itemGroup)
      .map(([_, item]) => item)
      .flat()
  }
  const getBulkQuoteItems = async (payload) => {
    const { quoteId = null } = payload || {}
    if (!quoteId) return
    try {
      loading.getBulkQuoteItems = true
      const bulkQuoteItems = await store.dispatch(
        'bulkQuote/getBulkQuoteItems',
        {
          id: quoteId
        }
      )
      items.value = parseItems(bulkQuoteItems)
    } catch (err) {
      console.error(err)
    } finally {
      loading.getBulkQuoteItems = false
    }
  }
  const getBulkQuoteItemInfo = async (payload) => {
    const { quoteId = null } = payload || {}
    if (!quoteId) return
    try {
      loading.getBulkQuoteItemInfo = true
      const response = await store.dispatch('bulkQuote/getBulkQuoteItemInfo', {
        id: quoteId
      })
      const { quote_price: quotePrice, total_item: totalItem } = response
      quotePercentage.value = quotePrice
      totalItems.value = totalItem
    } catch (err) {
      console.error(err)
    } finally {
      loading.getBulkQuoteItemInfo = false
    }
  }
  const updateQuotePercentage = async (payload = { quoteId: '' }) => {
    const { quoteId = '' } = payload || {}
    if (requestController.updateQuotePercentage)
      requestController.updateQuotePercentage.abort()
    requestController.updateQuotePercentage = new AbortController()

    try {
      loading.updateQuotePercentage = true
      await store.dispatch('bulkQuote/updateQuotePercentage', {
        id: quoteId,
        data: {
          quote_price: quotePercentage.value
        },
        signal: requestController.updateQuotePercentage.signal
      })
      loading.updateQuotePercentage = false
      return true
    } catch (err) {
      if (err.message === 'cancelled') return false
      console.error(err)
      loading.updateQuotePercentage = false
      return false
    } finally {
      loading.updateQuotePercentage = false
    }
  }
  const updateQuoteItem = async (payload = { quoteId: '', item: {} }) => {
    const { quoteId = '', item = {} } = payload || {}
    if (requestController.updateQuoteItem)
      requestController.updateQuoteItem.abort()
    requestController.updateQuoteItem = new AbortController()

    try {
      loading.updateQuotePercentage = true
      await store.dispatch('bulkQuote/updateQuoteItem', {
        id: quoteId,
        data: transformItem(item),
        signal: requestController.updateQuoteItem.signal
      })
      loading.updateQuotePercentage = false
      return true
    } catch (err) {
      if (err.message === 'cancelled') return false
      console.error(err)
      loading.updateQuotePercentage = false
      return false
    }
  }
  const deleteQuoteItem = async (payload = { quoteId: '', itemId: '' }) => {
    const { quoteId = '', itemId = '' } = payload || {}
    try {
      loading.deleteQuoteItem = true
      await store.dispatch('bulkQuote/deleteQuoteItem', {
        id: quoteId,
        data: {
          id: itemId
        }
      })
      return true
    } catch (err) {
      return false
    } finally {
      loading.deleteQuoteItem = false
    }
  }
  const resetManuallyPricedItems = () => {
    const updatedItems = _resetManuallyPricedItems(items.value)
    items.value = updatedItems
  }

  return {
    items,
    loading,
    quotePercentage,
    totalItems,
    confirmManuallyPricedModal,
    hasManuallyPricedItems,
    resetManuallyPricedItems,
    getBulkQuoteItems,
    getBulkQuoteItemInfo,
    updateQuotePercentage,
    updateQuoteItem,
    deleteQuoteItem
  }
}

export const useSearchItem = () => {
  const querySearch = ref()
  const products = ref([])
  const categories = ref([])
  const loading = ref(false)
  const isProductActiveButNoPrice = ref(false)
  const searchController = ref(null)
  const setCategories = (items = []) => (categories.value = items)
  const setProducts = (items = []) => (products.value = items)
  const searchItems = async (searchQuery = '') => {
    unsetItems()
    const query = searchQuery || querySearch.value
    if (!query) return
    if (searchController.value) searchController.value.abort()
    searchController.value = new AbortController()
    try {
      loading.value = true
      const searchResult = await store.dispatch('bulkQuote/searchItems', {
        params: {
          search: query
        },
        signal: searchController.value.signal
      })
      const { product: categories = {}, ...products } = searchResult.data
      isProductActiveButNoPrice.value = searchResult?.product_active_but_no_price == 1 ? true : false
      const parsedCategories = Object.entries(categories).map(
        ([_, category]) => ({
          name: category.product_name,
          ...category
        })
      ) || []
      const parsedProducts = Object.entries(products).map(
        ([categoryName, items]) => ({
          category_name: categoryName,
          products: items
        })
      ) || []
      setCategories(parsedCategories)
      setProducts(parsedProducts)
      loading.value = false
    } catch (err) {
      if (err.message === 'canceled') return
      console.error(err)
      loading.value = false
    }
  }
  const unsetItems = () => {
    products.value = []
    categories.value = []
  }
  return {
    querySearch,
    products,
    categories,
    loading,
    isProductActiveButNoPrice,
    searchItems,
    unsetItems
  }
}

export const useCreateBulkQuoteItem = (options = { multiple: false }) => {
  const selectedItems = ref([])
  const loading = ref(false)
  const isSingleInput = computed(() => {
    const { multiple: isMultiple = false } = options
    return !isMultiple
  })
  const addItem = (item, options = {}) => {
    if (isSingleInput.value) selectedItems.value = []
    const {
      top_category_id: parentCategoryId,
      parent_category_name: parentCategoryName
    } = item
    const isInvalidParentCategory = !(parentCategoryId && parentCategoryName)
    if (isInvalidParentCategory) return
    const matchedExistingGroupNameIndex = selectedItems.value.findIndex(
      (productGroup) =>
        productGroup.id === parentCategoryId &&
        productGroup.is_mac === item.is_mac
    )
    const hasMatchedExistingGroup = matchedExistingGroupNameIndex > -1
    const quotePercentage = options.quotePercentage || 0
    const transformedConditionPrices = transformItemConditionPrices(
      (item.conditions && item.conditions.data) || [],
      quotePercentage
    )
    const itemConditions = {
      ...item.conditions,
      data: transformedConditionPrices
    }
    const selectedItem = {
      ...item,
      conditions: itemConditions,
      qty: 1
    }

    if (!hasMatchedExistingGroup) {
      selectedItems.value.push({
        id: parentCategoryId,
        name: parentCategoryName,
        is_mac: selectedItem.is_mac,
        items: [selectedItem]
      })
      return
    }

    const matchedExistingItemIndex = selectedItems.value[
      matchedExistingGroupNameIndex
    ].items.findIndex((item) => {
      return selectedItem.is_custom === 0
        ? item?.product_id === selectedItem.product_id
        : item.category_id === selectedItem.category_id &&
            item.is_mac === selectedItem.is_mac
    })
    const hasMatchedExistingItem = matchedExistingItemIndex > -1
    if (!hasMatchedExistingItem) {
      selectedItems.value[matchedExistingGroupNameIndex].items.push(
        selectedItem
      )
      return
    }
    selectedItems.value[matchedExistingGroupNameIndex].items[
      matchedExistingItemIndex
    ].qty++
  }
  const createSingleItem = async (payload = { items: [], quoteId: null }) => {
    try {
      const { quoteId, items } = payload || {}
      loading.value = true
      const parsedItems = transformItemGroup(items)
      const selectedItem = parsedItems[0]
      await store.dispatch('bulkQuote/createQuoteItem', {
        id: quoteId,
        data: selectedItem
      })
      return true
    } catch (err) {
      console.error(err)
      return false
    } finally {
      loading.value = false
    }
  }
  const createMultipleItems = async (
    payload = { items: [], quoteId: null }
  ) => {
    try {
      const { quoteId, items } = payload || {}
      loading.value = true
      throw new Error('not implemented yet')
    } catch (err) {
      console.error(err)
      return false
    } finally {
      loading.value = false
    }
  }
  const createItem = async (payload = { quoteId }) => {
    const { quoteId } = payload || {}
    if (!quoteId) return false
    if (isSingleInput.value) {
      return await createSingleItem({ items: selectedItems.value, quoteId })
    }
    return await createMultipleItems({ items: selectedItems.value, quoteId })
  }
  return {
    selectedItems,
    loading,
    addItem,
    createItem
  }
}

export const useCreateBulkQuote = () => {
  const { me } = useUser()
  const loading = ref(false)
  const data = reactive({
    customer: {
      id: null,
      full_name: null,
      email: null,
      company: null,
      shipping_address1: null,
      shipping_address2: null,
      shipping_address_text: null,
      shipping_suite: null,
      shipping_city: null,
      shipping_phone: null,
      shipping_state: null,
      shipping_zip: null,
      shipping_country: null,
      shipping_expected_date_quote: null
    },
    sales_person: {
      name: [me.value.first_name || '', me.value.last_name || ''].join(' '),
      email: me.value.email
    },
    additional_options: {
      memo: null,
      custom_fields: [],
      add_shipping_label_number: 1
    },
    is_use_memo: false,
    is_use_custom_fields: false,
    products: [],
    selected_products: [],
    quote_price_percentage: 100
  })
  const isEmptyCustomField = computed(
    () => data.additional_options.custom_fields.length === 0
  )
  const hasEmptyCustomFieldValue = computed(
    () =>
      data.additional_options.custom_fields.filter((field) => !field.value)
        .length > 0
  )
  const isInvalidMemo = computed(
    () => data.is_use_memo && !data.additional_options.memo
  )
  const isInvalidCustomFields = computed(
    () =>
      data.is_use_custom_fields &&
      (hasEmptyCustomFieldValue.value || isEmptyCustomField.value)
  )
  const isInvalidProducts = computed(() => data.selected_products.length === 0)
  const isInvalidSalesPerson = computed(
    () => !data.sales_person.name || !data.sales_person
  )
  const isInvalidCustomer = computed(() => {
    return (
      !data.customer.full_name ||
      !data.customer.email ||
      !data.customer.company ||
      !data.customer.shipping_address_text ||
      !data.customer.shipping_expected_date_quote
    )
  })
  const isSubmitQuoteDisabled = computed(() => {
    return (
      isInvalidCustomer.value ||
      isInvalidProducts.value ||
      isInvalidSalesPerson.value ||
      isInvalidMemo.value ||
      isInvalidCustomFields.value
    )
  })
  const hasManuallyPricedItems = computed(() =>
    checkHasManuallyPricedItems(data.selected_products)
  )

  const addProduct = (item) => {
    const {
      top_category_id: parentCategoryId,
      parent_category_name: parentCategoryName
    } = item
    const isInvalidParentCategory = !(parentCategoryId && parentCategoryName)
    if (isInvalidParentCategory) return
    const matchedExistingGroupNameIndex = data.selected_products.findIndex(
      (productGroup) =>
        productGroup.id === parentCategoryId &&
        productGroup.is_mac === item.is_mac
    )
    const hasMatchedExistingGroup = matchedExistingGroupNameIndex > -1
    const transformedConditionPrices = transformItemConditionPrices(
      (item.conditions && item.conditions.data) || [],
      data.quote_price_percentage
    )
    const itemConditions = {
      ...item.conditions,
      data: transformedConditionPrices
    }
    const selectedItem = {
      ...item,
      conditions: itemConditions,
      qty: 1
    }
    if (!hasMatchedExistingGroup) {
      data.selected_products.push({
        id: parentCategoryId,
        name: parentCategoryName,
        is_mac: selectedItem.is_mac,
        items: [selectedItem]
      })
      return
    }

    const matchedExistingItemIndex = data.selected_products[
      matchedExistingGroupNameIndex
    ].items.findIndex((item) => {
      return selectedItem.is_custom === 0
        ? item?.product_id === selectedItem.product_id
        : item.category_id === selectedItem.category_id &&
            item.is_mac === selectedItem.is_mac &&
            item.is_custom === selectedItem.is_custom &&
            item.product_name === selectedItem.product_name
    })
    const hasMatchedExistingItem = matchedExistingItemIndex > -1
    if (!hasMatchedExistingItem) {
      data.selected_products[matchedExistingGroupNameIndex].items.push(
        selectedItem
      )
      return
    }
    data.selected_products[matchedExistingGroupNameIndex].items[
      matchedExistingItemIndex
    ].qty++
  }
  const createBulkQuote = async () => {
    try {
      loading.value = true
      const customFields = {}
      data.additional_options.custom_fields.forEach(
        (field) => (customFields[field.name] = field.value)
      )
      const payload = {
        customer: {
          ...data.customer,
          shipping_expected_date_quote: moment(
            data.customer.shipping_expected_date_quote
          ).format('DD/MM/YYYY')
        },
        sales_person: data.sales_person,
        additional_options: {
          memo: data.additional_options.memo,
          custom_fields: customFields,
          add_shipping_label_number: data.additional_options.add_shipping_label_number ?? 1
        },
        item_details: {
          quote_price: data.quote_price_percentage,
          item: transformItemGroup(data.selected_products)
        }
      }
      return await store.dispatch('bulkQuote/createBulkQuote', payload)
    } catch (err) {
      return null
    } finally {
      loading.value = false
    }
  }
  const resetManuallyPricedItems = () => {
    const itemGroup = [...data.selected_products]
    data.selected_products = _resetManuallyPricedItems(
      itemGroup,
      data.quote_price_percentage
    )
  }
  return {
    data,
    loading,
    isSubmitQuoteDisabled,
    hasManuallyPricedItems,
    confirmManuallyPricedModal,
    addProduct,
    createBulkQuote,
    resetManuallyPricedItems
  }
}

export const useBulkQuoteStatus = () => {
  const loading = ref(false)
  const statuses = ref([])
  const getQuoteStatuses = async () => {
    try {
      loading.value = true
      statuses.value = await store.dispatch('bulkQuote/getQuoteStatuses')
    } catch (err) {
      console.error(err)
    } finally {
      loading.value = false
    }
  }
  const updateQuoteStatus = async (payload = { quoteId, status }) => {
    const { quoteId = null, status = {} } = payload || {}
    const { id: statusId, step: statusStep } = status
    try {
      loading.value = true
      await store.dispatch('bulkQuote/updateQuoteStatus', {
        id: quoteId,
        data: {
          status_id: statusId,
          new_step: statusStep
        }
      })
      return true
    } catch (err) {
      console.error(err)
      return false
    } finally {
      loading.value = false
    }
  }
  return {
    loading,
    statuses,
    getQuoteStatuses,
    updateQuoteStatus
  }
}

export const useReconcileQuoteItem = () => {
  const selectedItem = reactive({
    id: null,
    category_id: null,
    top_category_id: null,
    is_custom: 0,
    is_mac: 0,
    product_id: null,
    product_name: null,
    qty: 1,
    imei_required: 0
  })
  const specs = reactive({
    mac: {
      Processor: {
        id: null,
        name: null,
        options: []
      },
      Memory: {
        id: null,
        name: null,
        options: []
      },
      'Hard Drive': {
        id: null,
        name: null,
        options: []
      },
      'Graphics Card': {
        id: null,
        name: null,
        options: []
      }
    },
    color: {
      id: null,
      name: null,
      options: []
    },
    condition: {
      id: null,
      name: null,
      price: 0,
      rule: [],
      options: [],
      price_from_source: 0
    }
  })
  const itemDetail = reactive({
    qty: 1,
    price: 0,
    imeiNotes: [
      {
        imei: null,
        notes: null
      }
    ]
  })
  const loading = reactive({
    getSpecs: {
      Processor: false,
      'Hard Drive': false,
      Memory: false,
      'Graphics Card': false,
      color: false,
      condition: false
    },
    reconcile: false
  })

  const isCustomItem = computed(() => selectedItem.is_custom == 1)
  const isMacItem = computed(() => selectedItem.is_mac == 1)
  const isValid = computed(() => {
    const isValidItemDetail =
      Number(itemDetail.qty) > 0 && Number(itemDetail.price) > 0
    const isValidColor = !!specs.color.id
    const isValidCondition = !!specs.condition.id || !!specs.condition.name
    const isValidMacSpec = isMacItem.value
      ? Object.entries(specs.mac).filter(([_, value]) => {
          return !value.id
        }).length === 0
      : true
    const isValidSpecs = isValidColor && isValidCondition && isValidMacSpec
    return isValidItemDetail && isValidSpecs
  })
  const emptySpecs = computed(() => {
    return Object.entries(specs.mac).filter(
      ([key, value]) => key !== 'product_id' && !value.id
    )
  })
  const nextSpec = computed(() => {
    if (emptySpecs.value.length === 0) return null
    const [key, value] = emptySpecs.value[0]
    return {
      key,
      value
    }
  })
  const specValues = computed(() =>
    Object.entries(specs.mac).reduce((acc, [key, value]) => {
      acc[key] = value.id || null
      return acc
    }, {})
  )

  const setSpecMac = async (payload = { key: null, item: {} }) => {
    const { key = null, item = {} } = payload
    if (!key) return console.warn(`Unable to set spec with null key`)
    const hasInvalidKey = !Object.keys(specs.mac).includes(key)
    if (hasInvalidKey) return console.warn(`${key} is invalid spec key`)

    const { id = null, name = null } = item
    specs.mac[key].id = id
    specs.mac[key].name = name

    // unset spec value that have greater index than updated spec key
    const updatedSpecKeyIndex = Object.keys(specs.mac).indexOf(key)
    Object.keys(specs.mac).forEach((key, specIndex) => {
      if (specIndex > updatedSpecKeyIndex) {
        specs.mac[key].id = null
        specs.mac[key].name = null
        specs.mac[key].options = []
      }
    })

    await getNextSpec()
  }
  const unsetSpecs = () => {
    Object.entries(specs.mac).forEach(([specKey, specValue]) => {
      specs.mac[specKey].id = null
      specs.mac[specKey].name = null
      specs.mac[specKey].options = []
    })
    specs.color.id = null
    specs.color.name = null
    specs.color.options = []
  }
  const unsetSelectedItem = () => {
    Object.assign(selectedItem, {
      id: null,
      category_id: null,
      top_category_id: null,
      is_custom: 0,
      is_mac: 0,
      product_id: null,
      product_name: null,
      imei_required: 0,
      qty: 1
    })
  }
  const unsetConditions = () => {
    specs.condition.id = null
    specs.condition.name = null
    specs.condition.options = []
  }
  const unsetItemDetail = () => {
    itemDetail.qty = 1
    itemDetail.price = 0
    itemDetail.imeiNotes = [
      {
        imei: '',
        notes: ''
      }
    ]
  }
  const unsetAll = () => {
    unsetSpecs()
    unsetSelectedItem()
    unsetConditions()
  }
  const setSelectedItem = (item = {}) => {
    if (!item) return
    unsetAll()
    Object.entries(selectedItem).forEach(([key, _]) => {
      const hasMatchedKey = item.hasOwnProperty(key)
      if (hasMatchedKey) {
        selectedItem[key] = item[key]
        return
      }
      console.warn(
        `Property "${key}" does not exist in the selected item, it may cause an error`
      )
    })
  }
  const setConditionOptions = (newConditions = []) => {
    specs.condition.options = newConditions
  }
  const setColorOptions = (colors = []) => {
    specs.color.options = colors
  }
  const getConditionsByCategoryId = async (payload = {}) => {
    const { categoryId, quoteId } = payload || {}
    try {
      loading.getSpecs.condition = true
      const conditions = await store.dispatch(
        'bulkQuote/getConditionsByCategoryId',
        {
          params: {
            category_id: categoryId,
            is_mac: selectedItem.is_mac,
            box_id: quoteId
          }
        }
      )
      setConditionOptions(conditions)
    } catch (err) {
      console.error(err)
    } finally {
      loading.getSpecs.condition = false
    }
  }
  const getConditionsByProductId = async (payload = {}) => {
    const { productId, quoteId } = payload || {}
    try {
      loading.getSpecs.condition = true
      const conditions = await store.dispatch(
        'bulkQuote/getConditionsByProductId',
        {
          params: {
            product_id: productId,
            is_mac: selectedItem.is_mac,
            buyback_type: 'mailin',
            is_editable: 0,
            condition: 1,
            box_id: quoteId
          }
        }
      )
      setConditionOptions(conditions)
    } catch (err) {
      console.error(err)
    } finally {
      loading.getSpecs.condition = false
    }
  }
  const getConditions = async (payload = { quoteId: null }) => {
    const { product_id: productId = null, category_id: categoryId = null } =
      selectedItem || {}
    const { quoteId = null } = payload || {}
    const isEmptyQuoteId = !quoteId
    if (isEmptyQuoteId)
      return console.warn('Unable to get condition, missing quote ID')

    if (isCustomItem.value) {
      const isEmptyCategoryId = !categoryId
      if (isEmptyCategoryId)
        return console.warn('Unable to get condition, missing category ID')
      await getConditionsByCategoryId({ categoryId, quoteId })
      return
    }
    const isEmptyProductId = !productId
    if (isEmptyProductId)
      return console.warn('Unable to get condition, missing item ID')
    await getConditionsByProductId({ productId, quoteId })
  }

  const getColors = async () => {
    try {
      loading.getSpecs.color = true
      const colorSpecs = await store.dispatch('bulkQuote/getItemColors')
      setColorOptions(colorSpecs)
    } catch (err) {
      console.error(err)
    } finally {
      loading.getSpecs.color = false
    }
  }

  const checkEligibleSpecs = (productId) => {
    const isEmptyProductId = !productId
    if (isEmptyProductId) {
      console.warn('Unable to get specs, missing item ID')
      return false
    }
    const isNonMacItem = !isMacItem.value
    const hasNoSpecs = isNonMacItem || isCustomItem.value
    if (hasNoSpecs) {
      console.warn('No specs available for non-mac item or custom item')
      return false
    }
    return true
  }

  const getNextSpec = async () => {
    let { product_id: productId } = selectedItem
    const isEmptyProductId = !productId
    if (isEmptyProductId)
      return console.warn('Unable to get specs, missing item ID')
    productId =
      String(productId || '').split('-').length > 0
        ? String(productId).split('-')[0]
        : productId

    const nextSpecKey = nextSpec.value ? nextSpec.value.key : 'condition'
    const currentFilledSpecs = specValues.value
    try {
      loading.getSpecs[nextSpecKey] = true
      const response = await store.dispatch('bulkQuote/getSpecs', {
        params: {
          product_id: productId,
          ...currentFilledSpecs,
          type: nextSpecKey,
          buyback_type: 'mailin',
          is_editable: 1,
          is_mac: 1,
          condition: nextSpecKey === 'condition' ? 1 : 0
        }
      })
      const isConditionSpec = nextSpecKey === 'condition'
      if (isConditionSpec) {
        specs.condition.options = response
        return
      }
      specs.mac[nextSpecKey].options = response
    } catch (err) {
      console.error(err)
    } finally {
      loading.getSpecs[nextSpecKey] = false
    }
  }

  // get all specs by product ID
  const getSpecsByProductId = async () => {
    const { product_id: productId } = selectedItem
    const isEligibleItem = checkEligibleSpecs(productId)
    if (!isEligibleItem) return

    const [productNumberId, processorId, memoryId, hardDriveId, graphicCardId] =
      String(productId || '').split('-')

    await getNextSpec()
    specs.mac.Processor.id = processorId
    await getNextSpec()
    specs.mac.Memory.id = memoryId
    await getNextSpec()
    specs.mac['Hard Drive'].id = hardDriveId
    await getNextSpec()
    specs.mac['Graphics Card'].id = graphicCardId
    await getNextSpec()

    Object.keys(specs.mac).forEach((key) => {
      const selectedSpec = specs.mac[key].options.find(
        (option) => option.id == specs.mac[key].id
      )
      if (selectedSpec) specs.mac[key].name = selectedSpec.name
    })
  }

  const transformReconcileItem = (
    payload = { items: {}, specs: {}, detail: {} }
  ) => {
    const { item = {}, specs = {}, detail = {} } = payload || {}

    const data = {
      product_id: item.product_id,
      product_name: item.product_name,
      category_id: item.category_id,
      category_name: null,
      condition_id: specs.condition.id,
      color_id: specs.color.id,
      top_category_id: item.top_category_id,
      is_custom: item.is_custom,
      is_mac: item.is_mac,
      qty: Number(detail.qty),
      price: detail.price,
      imei_sn: null,
      buyback_type: 'bulkquote',
      is_editable: 0,
      notes: null,
      imei_required: item.imei_required,
      condition: specs.condition.name,
      item_id: item.id,
      price_from_source: specs.condition.price_from_source
    }

    const isMacItem = item.is_mac && !item.is_custom
    if (isMacItem) {
      const isIncorrectlyFormattedMacId =
        data.product_id.split('-').length !== 5
      if (isIncorrectlyFormattedMacId) {
        const correctedMacId = [
          item.product_id,
          specs.mac.Processor.id,
          specs.mac.Memory.id,
          specs.mac['Hard Drive'].id,
          specs.mac['Graphics Card']
        ].join('-')
        data.product_id = correctedMacId
      }
    }
    const parsedIMEI = JSON.stringify(
      detail.imeiNotes.map((item) => item.imei || '')
    )
    const parsedNotes = JSON.stringify(
      detail.imeiNotes.map((item) => item.notes || '')
    )

    data.imei_sn = parsedIMEI
    data.notes = parsedNotes

    return data
  }

  const reconcileQuoteItem = async (payload = { quoteId: '' }) => {
    const { quoteId = '' } = payload || {}
    if (!quoteId) {
      console.warn('Unable to reconcile quote item, missing quote ID')
      return false
    }
    try {
      loading.reconcile = true
      const data = transformReconcileItem({
        item: selectedItem,
        specs: specs,
        detail: itemDetail
      })
      return await store.dispatch('bulkQuote/reconcileItem', {
        id: quoteId,
        data
      })
    } catch (err) {
      console.error(err)
      return null
    } finally {
      loading.reconcile = false
    }
  }

  return {
    selectedItem,
    specs,
    loading,
    isMacItem,
    isCustomItem,
    itemDetail,
    isValid,
    setSelectedItem,
    setSpecMac,
    getConditions,
    getSpecsByProductId,
    getColors,
    reconcileQuoteItem
  }
}
